import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button'


export default function PaperSheet(props) {
  const { classes } = props;

  return (
    <div>
      <Paper style={{padding: '1.5rem'}} elevation={1}>
        <Typography variant="h5" component="h3" style={{marginBottom: '20px'}}>
          {props.title}
        </Typography>
        <Typography component="p" style={{marginBottom: '20px'}}>
          {props.description}
        </Typography>
        <Button size="small" color="primary" onClick={() => window.location.href=props.website}>
          View More Info
        </Button>
        <Button size="small" color="primary" onClick={() => window.location.href=props.enquiry}>
          Make Enquiry
        </Button>
      </Paper>
    </div>
  );
}
